.card {
  background:black;
  border-radius: 2px;
  height: 250px;
  margin: 1rem;
  position: relative;
  width: 250px;
  -webkit-box-shadow: 0 3px 6px white, 0 3px 6px white;
          box-shadow: 0 3px 6px white, 0 3px 6px white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  /* padding: 20px;
  justify-content: space-around; */
  -ms-flex-line-pack: start;
      align-content: flex-start;
  overflow: auto;
 
  /* text-align: center; */
}

.image{
  height:235px;
  width: 235px;
  position:center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  overflow: auto;
 
 
}

.card:hover{
  -webkit-transform: scale(1.3);
  -ms-transform: scale(1.3);
  transform: scale(1.3);
}








